import { LAY_DANH_SACH_PHIM_ACTION } from "../const/QuanLyPhimConst"
import Axios from 'axios'
import { configure } from "@testing-library/react"
import { DOMAIN, USER_LOGIN, ACCESSTOKEN } from '../../Util/Config'



//------------------action gi API  (khong dispatch d liu trc tip ln reducer)------------------

export const layDanhSachPhimApiAction = () => {
    return dispatch => {
        //action tr v hm c tham s dispatch
        var promise = Axios({
            url: 'https://movie0706.cybersoft.edu.vn/api/QuanLyPhim/LayDanhSachPhim?maNhom=GP01',
            method: 'GET'
        })

        //thnh cng
        promise.then((res) => {
            dispatch(layDanhSachPhimApi(res.data))
            // setDSPhim(res.data)
        })

        //Tht bi
        promise.catch((error) => {
            console.log(error.response.data)
        })

    }
}

//vit api ly chi tit phim v
//async chy tun t, d qun l code cch 2 ca cc vit promise
export const layChiTietPhimApiAction = async (maPhim) => {

    return async (dispatch) => {

        try {
            //gi API ly d liu v
            let result = await Axios({
                url: `https://movie0706.cybersoft.edu.vn/api/QuanLyRap/LayThongTinLichChieuPhim?MaPhim=${maPhim}`,
                method: 'GET',
            })
            console.log(result.data)
            dispatch({
                type: 'LAY_CHI_TIET_PHIM',
                chiTietPhim: result.data
            })
        } catch (err) {
            console.log(err)
        }

    }

    //sau khi ly d liu dispatch ln reducer ri cp nht cho chi tit phim

}


export const layThongTinPhongVeAPIAction = async (maLichChieu) => {
    return async (dispatch) => {
        try {
            //gi API ly d liu v
            const { data, status } = await Axios({
                url: `https://movie0706.cybersoft.edu.vn/api/QuanLyDatVe/LayDanhSachPhongVe?MaLichChieu=${maLichChieu}`,
                method: 'GET',
            })

            console.log(data)

            //thnh cng ly d liu cp nht thng tin phng v
            if (status === 200) {

                dispatch({
                    type: 'THONG_TIN_PHONG_VE',
                    thongTinPhongVe: data
                })
            }

        } catch (err) {
            console.log(err)
        }
    }
}



//------------------action dispatch reducer------------------
export const layDanhSachPhimApi = (dataphim) => {
    return {
        type: LAY_DANH_SACH_PHIM_ACTION,
        dsPhim: dataphim
    }
}


